<template>
  <div>
    <Header></Header>

    <div v-if="errorMessage != null">
      <br><div class="alert alert-danger" role="alert" style="display:inline-block;">{{ errorMessage }}</div>
    </div>

    <div v-show="vm.isPlayingGame">
      <canvas id="myCanvas" width="600" height="500" style="border:1px solid #000000;">
      </canvas>
    </div>

    <div v-if="loading"><Spinner></Spinner></div>

    <h1>{{ vm.gameName }}</h1>
    <div>{{ vm.gameDescription }}</div>
    <div>Created by: {{ gameDetails.userId }}</div>
    <div>Created: {{ gameDetails.createdDate }}</div>
    <div>Updated: {{ gameDetails.updatedDate }}</div>
    <div v-if="vm.game">Engine version: {{ vm.game.templateVersion.S }}</div>

  <div v-show="false">
    <img v-for="image in vm.images" v-bind:key="image.id"
    :id="image.id"
    :src="'/mmg/game-images/' + image.id + '.png'">
  </div>

  </div>
</template>


<script>
import axios from 'axios';
import Header from '@/components/Header.vue';
import Spinner from '@/components/Spinner.vue';
import {loadEngine} from '@/lib/mmgUtil.js';

export default {
  name: 'PlayGame',
  components: {
    Header, Spinner
  },
  methods: {

    getGame: function (gameId) {

      let apiBaseUrl = process.env.VUE_APP_API_BASE_URL;
      let url = apiBaseUrl + 'games/' + gameId;
      const config = {};

      return axios.get(url, config);

    }

  },
  data() {
    return {
      game: null,
      loading: false,
      errorMessage: null,
      info: null,
      //gameJson: [],
      sessionId: null,
      gameDetails: {
        name: "",
        userId: "",
        createdDate: "",
        updatedDate: ""
      },
      vm: {
        isPlayingGame: false,
        oldLevel: null,
        newLevelVariable: null
      }
    }
  },
  mounted () {
   
    let that = this;

    that.loading = true;

    let gameId = this.$route.params.id;

    this
      .getGame(gameId)
      .then(function(response) {
        that.game = response.data.Item;
        let templateVersion = that.game.templateVersion.S;
        return loadEngine(templateVersion);
      })
      .then(function() {
        that.vm = new ActionTemplateController(); // eslint-disable-line no-undef
        that.vm.setGame(that.game);
        that.vm.initGame();
        that.loading = false;

        that.gameDetails.userId = that.vm.game.userId.S;
        that.gameDetails.createdDate = that.vm.game.createdDate.S;
        that.gameDetails.updatedDate = that.vm.game.updatedDate.S;
      });

  }
}
</script>


